import React, { useState, useContext, useMemo, useCallback } from 'react';
import Picker from 'react-mobile-picker';

import { Wrap, Service } from './';
import { getDates } from '../../../util/';
import { UIAlertSA } from '../../component/';
import { FormField } from '../../../component/';

const Personal = () => {

	const { userAuth: { data: user }, setUserAuthFN } = useContext(Service.User.Context);

	const [ status, setStatus ] = useState(1);

	const _dateParse = useCallback((date) => {

		const _parsed = (date || '').match(/([\d]{4})([\d]{1,2})([\d]{1,2})/);

		if (!_parsed) {
			return {
				year: null,
				month: null,
				day: null,
			};
		}

		const _date = {
			year: + _parsed[1],
			month: + _parsed[2],
			day: + _parsed[3],
		};

		return _date;

	}, []);

	const [ form, setForm ] = useState({
		email: user.email || '',
		name: user.realName || '',
		birthday: _dateParse(user.birthday),
		number: user.telephone || '',
		qq: user.qq || '',
		wechat: user.wechat || '',
	});

	const [ subform, setSubform ] = useState({
		email: !!user.email,
		name: !!user.realName,
		birthday: !!_dateParse(user.birthday).year,
		number: !!user.telephone,
		qq: !!user.qq,
	});

	const [ datePicker, toggleDatePicker ] = useState(false);

	const _dateFormat = useCallback((date) => date.year ? `${date.year}${date.month}${date.day}` : '', []);

	const _dates = useMemo(() => getDates({ date: form.birthday, year: [ 18, 150 ] }), [ form.birthday ]);

	const fields = [
		{
			field: {
				label: '',
			},
			input: {
				id: 'email',
				name: 'email',
				type: 'email',
				placeholder: '',
			},
		},
		{
			field: {
				label: '',
			},
			input: {
				id: 'name',
				name: 'name',
				type: 'text',
				placeholder: '',
			},
		},
		{
			field: {
				label: '',
			},
			input: {
				id: 'birthday',
				name: 'birthday',
				type: 'text',
				placeholder: '',
			},
		},
		{
			field: {
				label: '',
			},
			input: {
				id: 'number',
				name: 'number',
				type: 'text',
				placeholder: '',
			},
		},
		{
			field: {
				label: 'QQ',
			},
			input: {
				id: 'qq',
				name: 'qq',
				type: 'text',
				placeholder: 'QQ',
			},
		},
	];

	const onChange = e => {

		const { name, value } = e.target;

		setForm(f => ({
			...f,
			[name]: value,
		}));

	}

	const [ alert, setAlert ] = useState({
		shown: false,
		title: '',
		message: '',
		onClose: () => hideAlert(),
	});

	const showAlert = (title, message) => {

		setAlert(a => ({ ...a, shown: true, title, message }));

	}

	const hideAlert = () => {

		setAlert(a => ({ ...a, shown: false }));

	}

	const update = () => {

		setStatus(0);

		if (!form.birthday.year) {
			setStatus(1);
			return void showAlert('', '');
		}

		let shouldUpdate = false;
		for (const i in subform) {
			if (!subform[i]) {
				shouldUpdate = true;
				break;
			}
		}

		if (!shouldUpdate) {
			setStatus(1);
			return void showAlert('', 'CS');
		}

		console.info('You\'re updating profile information:', form);

		Service.User.update({
			...Service.User.read(),
			...form,
			birthday: _dateFormat(form.birthday),
			realname: form.name,
			phone: form.number,
		}).promise.then(r => {

			console.info(' You have successfully updated profile information:', r.info);

			setStatus(1);

			showAlert('', '');

			const _sf = {};

			for (const i in fields) {
				const f = fields[i];
				_sf[f.input.name] = !!form[f.input.name];
			}

			setSubform(_sf);

			Service.User.session({
				...Service.User.read()
			}).promise.then(r => setUserAuthFN(1, r.info));

		}, e => {

			console.warn('Unable to update profile information:', e);

			setStatus(1);

		});

	}

	return (
		<Wrap className="profile-personal" name="" isLoading={!status}>
			<div className="fields">
				{fields.map((f, i) => (
					<FormField
						key={i}
						field={{
							...f.field,
							select: f.input.id === 'birthday',
							onClick: () => {

								if (!form.birthday.year) {

									const _d = getDates({ date: {}, year: [ 18, 150 ] });

									setForm(f => ({
										...f,
										birthday: {
											year: _d.year[0],
											month: _d.month[0],
											day: _d.day[0],
										},
									}));

								}

								toggleDatePicker(true);

							},
							value: () => _dateFormat(form.birthday),
						}}
						input={{
							...f.input,
							onChange,
							value: form[f.input.name],
							disabled: subform[f.input.name],
						}} />
				))}
			</div>
			<div className="submit">
				<button className="button-stylized" onClick={update}></button>
			</div>
			<div className={`date-select-overlay${datePicker ? ' shown' : ''}`}>
				<div className="picker-wrap">
					<div className="picker-head">
						<button onClick={() => toggleDatePicker(false)}></button>
						<p></p>
						<button onClick={() => toggleDatePicker(false)}></button>
					</div>
					<div className="picker-body">
						{datePicker ? (
						<Picker
							height={120}
							valueGroups={{ ...form.birthday }}
							optionGroups={{ ..._dates }}
							onChange={(k, v) => setForm({ ...form, birthday: { ...form.birthday, [k]: v } })}/>
						) : null}
					</div>
				</div>
			</div>
			<UIAlertSA {...alert} />
		</Wrap>
	);

}

export default Personal;
